body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

textarea {
  width: 30em;
  height: 30em;
  margin: 0 0.5em;
}

del, ins, .com {
  font-weight: bold;
}

body {
  font: 13px Verdana, sans-serif;
}
.boxes {
  float: left;
  margin: 0 auto;
}
#container {
  border: 3px solid  #6495ED;
  display: inline-block;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  border-radius: 12px;
  padding: 15px 15px;
}
h1 {
  margin-top: 0px;
  border-bottom: 1px solid  #6495ED;
  padding: 0 0 .5em 0;
  color: #483D8B;
  text-align: left;
}
h2 {
  color: #483D8B;
  text-align: left;
}
.panel {
  color: #483D8B;
  font-weight: bold;
  margin-left: 5px;
  text-align: left;
  clear: both;
}
#output {
  width: 30em;
  height: 30em;
  margin: 0 0.5em;
  overflow: auto;
  text-align: left;
  word-wrap: break-word;
  white-space: pre-wrap;
}
